/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
  color: #fff;
  font-family: "Exo";
  font-size: 1rem;
  line-height: 1.5; }

/* Headings */
h1, h2, h3, h4, h5, h6 {
  clear: both; }

/* Copy */
p {
  margin-bottom: 1.5rem; }

dfn, cite, em, i {
  font-style: italic; }

blockquote {
  margin: 0 1.5rem; }

address {
  margin: 0 0 1.5rem; }

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: .9375rem;
  line-height: 1.6;
  margin-bottom: 1.6rem;
  max-width: 100%;
  overflow: auto;
  padding: 1.6rem; }

code, kbd, tt, var {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: .9375rem; }

abbr, acronym {
  border-bottom: 1px dotted #666;
  cursor: help; }

mark, ins {
  background: #fff9c0;
  text-decoration: none; }

big {
  font-size: 125%; }

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
  font-size: 1rem; }

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit; }

body {
  background: #222222;
  /* Fallback for when there is no custom background color defined. */ }

blockquote,
q {
  quotes: '' ''; }
  blockquote:before, blockquote:after,
  q:before,
  q:after {
    content: ''; }

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5rem; }

ul,
ol {
  margin: 0 0 1.5rem 3rem; }

ul {
  list-style: disc; }

ol {
  list-style: decimal; }

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5rem; }

dt {
  font-weight: bold; }

dd {
  margin: 0 1.5rem 1.5rem; }

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

table {
  margin: 0 0 1.5rem;
  width: 100%; }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Buttons */
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  background: none;
  border: 2px solid #00cc00;
  border-radius: 3px;
  color: #00cc00;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  padding: .65rem 2.25rem; }
  @media screen and (min-width: 620px) {
    button,
    input[type="button"],
    input[type="reset"],
    input[type="submit"] {
      font-size: 1.25rem;
      font-weight: 500; } }
  button:hover, button:active, button:focus,
  input[type="button"]:hover,
  input[type="button"]:active,
  input[type="button"]:focus,
  input[type="reset"]:hover,
  input[type="reset"]:active,
  input[type="reset"]:focus,
  input[type="submit"]:hover,
  input[type="submit"]:active,
  input[type="submit"]:focus {
    border-color: #00cc00;
    color: #00cc00;
    filter: brightness(80%); }

#new-quote-button {
  margin-top: 30px; }

/* Fields */
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
  color: #666;
  border: 1px solid #ccc;
  border-radius: 3px; }
  input[type="text"]:focus,
  input[type="email"]:focus,
  input[type="url"]:focus,
  input[type="password"]:focus,
  input[type="search"]:focus,
  textarea:focus {
    color: #111; }

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
  padding: 3px; }

textarea {
  padding-left: 3px;
  width: 100%; }

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/* Links */
a {
  color: #00cc00;
  text-decoration: none; }
  a:hover, a:focus, a:active {
    color: #00cc00;
    filter: brightness(80%); }
  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

/* Menus */
.main-navigation {
  display: flex;
  justify-content: space-between;
  margin-top: 75px;
  height: 15px; }
  .main-navigation ul {
    list-style: none;
    margin: 0;
    padding: 0; }
    .main-navigation ul a {
      display: block;
      text-decoration: none;
      border-right: 1px solid #fff;
      padding: 0 15px; }
    .main-navigation ul li:last-child a {
      border-right: none; }
      @media screen and (min-width: 620px) {
        .main-navigation ul li:last-child a {
          border-right: 1px solid #fff; } }
    .main-navigation ul li:first-child a {
      padding-left: 0; }

/* Small menu. */
@media screen and (min-width: 768px) {
  .main-navigation ul {
    display: flex; } }

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
  margin: 0 0 1.5rem;
  overflow: hidden; }

.menu-nav-container ul {
  display: flex;
  justify-content: space-between; }

.site-info {
  margin-left: 13px;
  font-weight: 300; }
  @media screen and (min-width: 620px) {
    .site-info {
      display: flex;
      align-items: baseline; } }
  @media screen and (min-width: 620px) {
    .site-info p {
      margin-left: 15px; } }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden; }
  .screen-reader-text:focus {
    background-color: #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: #21759b;
    display: block;
    font-size: .875rem;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5rem; }

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5rem; }

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto; }

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clearfix {
  clear: both;
  content: "";
  display: table; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/* Posts and pages */
.sticky {
  display: block; }

.hentry {
  margin: 0 0 1.5rem; }

.updated:not(.published) {
  display: none; }

.single .byline,
.group-blog .byline {
  display: inline; }

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5rem 0 0; }
  @media screen and (min-width: 620px) {
    .page-content p,
    .entry-content p,
    .entry-summary p {
      font-size: 1.75rem; } }

.page-links {
  clear: both;
  margin: 0 0 1.5rem; }

@media screen and (min-width: 1080px) {
  .site-main {
    text-align: center; } }

.home-content {
  margin: 15px;
  font-size: 1.25rem;
  max-width: 100vw;
  font-weight: 300; }
  .home-content h2 {
    font-size: 20px;
    font-weight: 300; }
  .home-content .entry-title {
    display: inline; }
  .home-content span.source {
    font-style: italic; }
  @media screen and (min-width: 1080px) {
    .home-content {
      width: 420px;
      display: inline-block; }
      .home-content p {
        text-align: left; } }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/
/* Home */
.main-logo {
  width: 300px;
  height: auto;
  margin-top: 60px;
  margin-left: 15px; }
  @media screen and (min-width: 620px) {
    .main-logo {
      width: 500px; } }
  @media screen and (min-width: 1080px) {
    .main-logo {
      width: 700px; } }

@media screen and (min-width: 1080px) {
  .site-branding {
    text-align: center; } }

